:root {
  --bg: #111;
  --text: #fff;
  --accent: #ffcc33;
  --container-max: 1500px;
}

body {
  margin: 0;
  font-family: 'Pretendard', sans-serif;
  background: var(--bg);
  color: var(--text);
}

section {
  scroll-margin-top: 100px;
}

/* Header */
.header {
  position: sticky;
  top: 0;
  background: #000;
  padding: 20px 0;
  z-index: 1000;
}
.nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.nav-links {
  display: flex;
  gap: 40px;
  margin-right: 60px;
  list-style: none;
}
.nav-links a {
  color: var(--text);
  text-decoration: none;
  font-size: 20px;
  transition: 0.3s;
}
.nav-links a:hover {
  color: var(--accent);
}

/* „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°Home „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°*/
.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  position: relative;
}

.hero-wrapper {
  text-align: center;
  position: relative;
}

.hero-text {
  position: absolute;
  top: -50px;
  left: 50%;
  transform: translateX(-50%) rotate(6deg);
  font-size: 18px;
  color: #ccc;
}

.hero-main {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
}

.hero-j {
  position: relative;
  left: 90px;
  top: 20px;
  width: 400px;
  transform: rotate(-2deg);
  z-index: 1;
}

.hero-y {
  width: 270px;
  transform: rotate(3px);
  position: relative;
  right: 110px;
  bottom: 180px;
}

/* Ìè¥ÎùºÎ°úÏù¥Îìú ÏòÅÏó≠ */
.hero-photo {
  position: relative;
  top: -40px;
  right: 20px;
  width: 400px;
  height: 470px;
  transform: rotate(-2deg);
}

.profile-bg {
  width: 100%;
  height: 100%;
  display: block;
}

/* ÌîÑÎ°úÌïÑ ÏÇ¨ÏßÑ Í≤πÏπòÍ∏∞ */
.jyphoto {
  position: absolute;
  top: 30px; /* Ìè¥ÎùºÎ°úÏù¥Îìú ÎÇ¥Î∂Ä ÏúÑÏπò Ï°∞Ï†ï */
  left: 52%;
  transform: translateX(-50%);
  width: 330px;
}

.name {
  position: absolute;
  bottom: 5px;
  left: 70%;
  transform: translateX(-50%) rotate(8deg);
  color: #000;
  font-weight: 600;
  font-size: 32px;
}

/* „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°About„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö° */
.about {
  color: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.about-title {
  position: relative;
  left: 50px;
  top: 20px;
}

.about-title-img {
  width: 500px;
  transform: rotate(-4deg);
  display: block;
}

/* üîπ Î©îÏù∏ 3Ïó¥ Î†àÏù¥ÏïÑÏõÉ */
.about-content {
  display: grid;
  grid-template-columns: 1fr 1.2fr 1.1fr;
  gap: 60px;
  align-items: start;
  justify-content: center;
}

/* ÏôºÏ™Ω ÌîÑÎ°úÌïÑ */
.about-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  position: relative;
  top: 70px;
}

.photo-wrap {
  position: relative;
  width: 260px;
}

.masking {
  position: absolute;
  top: -50px;
  left: 80px;
  width: 100px;
  transform: rotate(-4deg);
  z-index: 2;
}

.photo-frame {
  background: #fff;
  padding: 15px;
  transform: rotate(-8deg);
  position: relative;
  left: 20px;
}

.photo-main {
  width: 200px;
  display: block;
}

.photo-person {
  position: absolute;
  top: 25px;
  left: 50%;
  transform: translateX(-50%) rotate(6deg);
  width: 250px;
}


.photo-name {
  color: #000;
  text-align: center;
  font-weight: 600;
  margin-top: 20px;
  font-size: 25px;
}

.photo-name span {
  font-weight: 400;
  font-size: 20px;
}

.about-info {
  font-size: 15px;
  line-height: 1.6;
  margin-top: -15px;
}

/* Ï§ëÏïô ÏΩòÌÖêÏ∏† */
.about-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 30px;
  position: relative;
  top: 50px;
}

.about-section h3 {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 10px;
  border-bottom: 1px solid #444;
  padding-bottom: 5px;
}
.about-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
  color: #ddd;
  line-height: 1.8;
}

/* „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö° Ïò§Î•∏Ï™Ω Ïä§ÌÇ¨ „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°*/
.about-right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 40px; /* Í∑∏Î¶¨Îìú ÏïàÏ™ΩÏúºÎ°ú Ïù¥Îèô */
}

.about-right h3 {
  border-bottom: 1px solid #444;
  padding-bottom: 5px;
}

.skill-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.skill-list li {
  margin-bottom: 12px;
}
.skill-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.skill-icon {
  width: 20px;
  margin-right: 10px;
}
.skill-label span {
  flex: 1;
  font-size: 14px;
}
.skill-num {
  color: #aaa;
  font-size: 13px;
  margin-left: 10px;
}
.bar {
  height: 8px;
  background: #333;
  border-radius: 4px;
  margin-top: 4px;
}
.fill {
  height: 100%;
  background: var(--accent);
  border-radius: 4px;
}

/* Î∞òÏùëÌòï */
@media (max-width: 1100px) {
  .about-content {
    grid-template-columns: 1fr 1fr;
  }
  .about-right {
    margin-top: 40px;
  }
}

@media (max-width: 800px) {
  .about-content {
    grid-template-columns: 1fr;
    text-align: center;
  }
  .about-right {
    align-items: center;
  }
}



/* „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°Works „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°*/
/* Ï†ÑÏ≤¥ ÏÑπÏÖò */
.works-section {
  color: #fff;
  text-align: center;
  padding: 30px 0;
}

/* ÏÉÅÎã® WORKS Ïä§Ìã∞Ïª§ */
.works-title {
  margin-bottom: 30px;
}
.works-logo {
  width: 350px;
  display: inline-block;
}

/* ÌïÑÌÑ∞ Î≤ÑÌäº */
.filter-buttons {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 50px;
}
.filter-buttons button {
  background: #fff;
  border: none;
  border-radius: 50px;
  padding: 10px 28px;
  font-size: 14px;
  cursor: pointer;
  transition: 0.3s;
  color: #000;
}
.filter-buttons .active {
  background: #ffce3a;
  font-weight: 600;
}

/* Ïä¨ÎùºÏù¥Îìú Î†àÏù¥ÏïÑÏõÉ */
.work-display {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 25px;
  position: relative;
  top: -15px;
}

.arrow {
  font-size: 40px;
  color: #00d4d4;
  cursor: pointer;
  user-select: none;
  transition: 0.3s;
}
.arrow:hover {
  transform: scale(1.2);
}

/* Ïπ¥Îìú ÏòÅÏó≠ */
.work-card {
  background: #f9f6f1;
  color: #000;
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px 60px;
  max-width: 950px;
  width: 90%;
  min-height: 400px;
  opacity: 1;
  transition: opacity 0.6s ease-in-out, transform 0.4s ease-in-out;
}

/* Î∂ÄÎìúÎü¨Ïö¥ Ïä¨ÎùºÏù¥Îìú Ìö®Í≥º */
.fade {
  animation: fadeIn 0.6s ease-in-out;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.laptop-wrap {
  flex: 1;
  text-align: left;
}
.laptop {
  width: 95%;
  max-width: 420px;
}

/* Ïò§Î•∏Ï™Ω ÌÖçÏä§Ìä∏ */
.work-text {
  flex: 1;
  text-align: left;
  padding-left: 40px;
}

.work-text h3 {
  text-align: center;
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 10px;
}
.work-icons {
  display: flex;
  justify-content: center; /* Ï§ëÏïô Ï†ïÎ†¨ ÌïµÏã¨ */
  gap: 10px;
  margin-bottom: 20px;
}

.work-icons img {
  width: 36px;
  margin-right: 8px;
}

.work-text p {
  font-size: 14px;
  line-height: 1.7;
  color: #333;
  margin: 10px 0;
}

.work-meta {
  margin-top: 20px;
  font-size: 15px;
  line-height: 1.8; /* üîπ Ï§ÑÍ∞ÑÍ≤©ÏùÑ ÎÑìÌûàÎäî ÌïµÏã¨ Î∂ÄÎ∂Ñ */
  color: var(--text);
}

/* Î∞òÏùëÌòï */
@media (max-width: 950px) {
  .work-card {
    flex-direction: column;
    text-align: center;
    padding: 30px;
  }
  .work-text {
    padding-left: 0;
  }
  .laptop {
    width: 80%;
  }
}


/* „Ö°„Ö°„Ö°„Ö°„Ö°Contact „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°*/
.contact-section {
  padding: 100px 0;
  color: #fff;
  display: flex;
  justify-content: center;
}

.contact-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 80px;
  width: 80%;
  max-width: 1100px;
}

/* --- ÏôºÏ™Ω ÏòÅÏó≠ --- */
.contact-left {
  flex: 1;
  text-align: center;
}

.contact-image {
  width: 380px;
  max-width: 100%;
  margin-bottom: 40px;
}

.contact-email {
  font-size: 16px;
  color: #fff;
  cursor: default;
}

.copy-icon {
  margin-left: 8px;
  cursor: pointer;
  font-size: 18px;
}

/* --- Ïò§Î•∏Ï™Ω Ìèº ÏòÅÏó≠ --- */
.contact-form {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.contact-form label {
  font-size: 14px;
  color: #ccc;
  margin-bottom: -10px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 10px 0;
  border: none;
  border-bottom: 1px solid #666;
  background: transparent;
  color: #fff;
  font-size: 15px;
  outline: none;
  resize: none;
}

.contact-form textarea {
  height: 100px;
}

.contact-form button {
  margin-top: 20px;
  padding: 14px;
  border-radius: 12px;
  border: none;
  background: #fff;
  color: #000;
  font-weight: 600;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s;
}

.contact-form button:hover {
  background: #ffcc33;
}

/* --- Î∞òÏùëÌòï --- */
@media (max-width: 900px) {
  .contact-container {
    flex-direction: column;
    text-align: center;
  }

  .contact-left,
  .contact-form {
    width: 100%;
  }

  .contact-image {
    width: 300px;
  }
}


/*„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö° Footer „Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°„Ö°*/
.footer {
  text-align: center;
  padding: 40px 0;
  color: #888;
}

/* Responsive */
@media (max-width: 1100px) {
  .about-grid {
    grid-template-columns: 1fr;
  }
}
